.rest-conf {
  @include flex(100%);
  padding: 92px 0 120px;
  &-list {
    @include flex-container(row, nowrap, center);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    &-outer {
      position: relative;

      margin: 57px 0 48px;
    }
    &__item {
      padding: 0 40px 24px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active,
      &:hover {
        color: $gold;
      }
    }
  }
  &-preview {
    &-list {
      &__item {
        display: none;
        &--active {
          display: block;
        }
      }
    }
  }
  &-row {
    @include flex-container();
    .tns {
      &-nav + button,
      &-controls {
        display: none;
      }
      &-nav {
        & > button {
          display: none;
        }
      }
    }
  }
  &__title {
    @include font(40px, 40px);
    font-weight: 300;
  }
  &-slider {
    &-row {
      margin: 0 -15px;
      @include flex-container(row, wrap, normal);
      max-width: 780px;
      transform: none !important;
      .rest-conf-slider-image {
        @include flex(156px);
        max-height: 79px;
        padding: 0 15px;
      }
      .rest-conf-slider__img {
        opacity: 0.8;
      }
      .rest-conf-slider-container {
        padding: 0 15px;
      }
    }
    &-container {
      @include flex(750px);
      margin-right: 150px;
    }
    &-image {
      max-width: 750px;
      max-height: 405px;
      margin-top: 30px;
      overflow: hidden;
    }
    &__img {
      @include img();
    }
    &-controls {
      margin: 6px 0 0;
      @include flex-container(row, nowrap, normal, space-between);
      &-col {
        @include flex-container(row, nowrap, center);
        &:first-child {
          @include flex(150px);
        }
        &:last-child {
          @include flex(150px);
        }
      }
    }
    &-counter {
      margin-right: 20px;
    }
  }
  &-features {
    margin-top: 55px;
  }
  &-feature {
    margin-bottom: 13px;
    &__name {
      margin-bottom: -6px;

      color: $grey-lightest;
    }
    &__value {
      @include font(30px, 40px);
      font-weight: 300;
    }
  }
  &-buttons {
    margin-top: 35px;
    @include flex-container(column);
    .btn--rest-conf {
      &:first-child {
        margin-bottom: 30px;
      }
    }
  }
}

@import "media";
