.conference-info {
  @include flex(100%);
  padding: 120px 0;
  &-row {
    @include flex-container($justify-content: space-between);
  }
  &-col {
    @include flex(50%);
    &:first-child {
      h2 {
        @include font(40px, 40px);
        font-weight: 300;
      }
      p:not(.conference-info-desc),
      ul:not(.conference-info-list) {
        display: none;
      }
    }
    &:last-child {
      max-width: 420px;
      margin-top: 9px;
      h2 {
        display: none;
      }
    }
    li {
      @include flex-container(row, nowrap, center);
      margin-top: 15px;
      &:before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 14px;
        margin-right: 10px;
        background: url(../images/svg/check.svg) no-repeat center;
      }
    }
  }
  &-title {
    @include font(40px, 40px);
    font-weight: 300;
    &--gold {
      color: $gold;
    }
  }
  &-list {
    &__item {
      @include flex-container(row, nowrap, center);
      margin-top: 15px;

      color: $grey-lightest;
      &:before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 14px;
        margin-right: 10px;
        background: url(../images/svg/check.svg) no-repeat center;
      }
    }
  }
}

@import "media";
