h1,
h2,
h3,
h4,
h5,
h6,
ul,
li {
  margin: 0;
  padding: 0;
}
ul {
  list-style: none;
}
::selection {
  background: $gold;
}
.container {
  @include container();
  @include xs(max-width, 480px);
  @include sm(max-width, 768px);
  @include md(max-width, 1024px);
  @include lg(max-width, 1280px);
  //@include xl(max-width, 1440px);
  padding: 0 40px;
}
html {
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
}
.html--unscroll {
  overflow-y: hidden;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;

  outline: none !important;
}
img {
  max-width: 100%;
}
:root {
  --animate-duration: 0.3s;
}
.body {
  position: relative;

  @include flex-container(column, wrap);
  max-width: 100%;
  min-height: 1000px;
  margin: 0 auto;

  font-family: "Lato", "Open Sans", "Arial", sans-serif;
  @include font(15px, 20px);
  font-weight: 400;

  color: #fff;
  background-color: $grey-lighter;
  .main {
    @include flex(100%, 1, 0);
    margin-top: 80px;
    &--top {
      margin-top: 80px;
    }
  }
}
body section {
  &:nth-child(2n) {
    background: $grey-darker;
  }
}
.section-title {
  font-family: "Lato", "Open Sans", "Arial", sans-serif;
  font-weight: 300;
  @include font(60px, 60px);

  color: #fff;
}
.section-title--gold {
  color: $gold;
}
.btn {
  position: relative;

  display: block;
  overflow: hidden;
  width: 100%;
  max-width: 250px;
  height: auto;
  max-height: 60px;
  margin: 0;
  //padding: 18px 40px;
  border: none;
  outline: 0;

  white-space: nowrap;
  text-overflow: ellipsis;
  user-select: none;
  @include font(15px, 60px);
  text-decoration: none;
  text-transform: uppercase;
  text-align: center;
  color: #fff;

  background: $gold;
  cursor: pointer;

  transition: background-color 400ms;
  &--transparent {
    //padding: 16px 38px;
    border: 1px solid $gold;

    color: $gold;

    background-color: transparent;
    &:hover {
      color: #fff;
      .btn-icon {
        background-image: url(../images/svg/dish.svg);
      }
    }
    .btn-text {
      left: -5px;
    }
  }
  &-text {
    position: relative;
    z-index: 5;

    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  &-icon {
    width: 24px;
    height: 24px;
    margin-right: 5px;
    background: top url(../images/svg/dish-gold.svg) no-repeat;
    background-size: contain;
  }
  &:hover {
    background-color: #b09d5f;
  }
  span.ripple {
    position: absolute;

    border-radius: 50%;

    background-color: #968651;

    transform: scale(0);
    animation: ripple 500ms ease-in-out;
  }
  @keyframes ripple {
    to {
      transform: scale(4);
      opacity: 0;
    }
  }
}
.underline {
  position: relative;
  top: -2px;
  left: 0;

  height: 2px;
  width: 100px;

  background: $gold;

  transition: all 250ms linear;
}
.animated,
.slider {
  &__icon,
  &__next,
  &__prev,
  &__full {
    position: relative;

    min-width: 48px;
    min-height: 48px;
    border-radius: 50%;

    cursor: pointer;
    outline: none;
    background: url(../images/svg/arrow.svg) no-repeat center;
    &:before,
    &:after {
      @include animate(0.25s, ease-in-out);
      content: "";
      position: absolute;
      left: 25%;
      top: 25%;

      width: 50%;
      height: 50%;
      border-radius: 100%;

      opacity: 0;
      background-color: rgba(255, 255, 255, 0.05);
    }
    /*&:after {
      z-index: 1;

      background-color: rgba(255, 255, 255, 0.1);
    }*/
    &:hover {
      &:before {
        left: 0;
        top: 0;

        width: 100%;
        height: 100%;

        opacity: 1;
      }
    }
    /*&:active {
      &:after {
        left: 0;
        top: 0;

        width: 100%;
        height: 100%;

        opacity: 1;
      }
    }*/
    /*&:hover {
      background-color: rgba(255, 255, 255, 0.05);
    }*/
    &:active {
      background-color: rgba(255, 255, 255, 0.1);
    }
  }
  &__prev {
    transform: rotate(180deg);
  }
  &__full {
    background: url(../images/svg/fullscreen.svg) no-repeat center;
  }
  &__icon {
    background-image: url(../images/svg/carousel.svg);
    &--active {
      background-image: url(../images/svg/carousel-gold.svg);
      &.slider__icon--module {
        background-image: url(../images/svg/module-gold.svg);
      }
    }
    &--module {
      background-image: url(../images/svg/module.svg);
    }
  }
}
.form {
  &-group {
    width: 300px;
    max-width: 300px;
    /*&:nth-last-child(1) {
      margin-top: 18px;
    }*/
  }
  &__item {
    width: 300px;
    max-width: 300px;
    margin-bottom: 22px;
    border: none;
    border-bottom: 1px solid $grey;
    padding-bottom: 6px;

    background: transparent;

    color: #fff;

    &::placeholder {
      color: $grey;

      @include font(14px, 20px);
    }
    &:focus {
      outline: none;
    }
    &:focus,
    &:not(:placeholder-shown) {
      + .label-text {
        position: relative;
        bottom: 72px;

        @include font(12px, 15px);
      }
    }
    &:invalid:not(:placeholder-shown) + .form--error {
      display: block;
    }
    &:invalid:not(:placeholder-shown) {
      border-bottom: 1px solid rgb(134, 19, 19) !important;
    }
    &--textarea {
      position: relative;

      width: 300px;
      min-width: 300px;
      max-width: 300px;
      height: 85px;
      min-height: 85px;
      max-height: 200px;

      text-align: left;
      line-height: 30px;
      & ~ .label-text {
        bottom: 114px;
      }
      &:focus,
      &:not(:placeholder-shown) {
        & + .label-text {
          bottom: 132px;
        }
      }
    }
  }
}
.label {
  position: relative;

  display: block;
  width: 300px;
  max-width: 300px;

  @include font(14px, 20px);

  color: $grey;
  &-text {
    position: relative;
    bottom: 50px;

    transition: bottom 0.2s;
  }
  &--textarea {
    position: relative;
    &:before,
    &:after {
      position: absolute;
      top: 25px;

      content: "";
      display: block;
      width: 300px;
      height: 1px;

      background: $grey;
    }
    &:after {
      top: 54px;
    }
  }
}
.oc-text {
  &-grey-lightest {
    color: #86868c;
  }
  &-gold {
    color: $gold;
  }
}

@import "media";
