.header-form {
  position: fixed;
  z-index: 21;

  @include flex(100%);

  transform: translate(0, 0);
  transition: transform 900ms linear;
  &--hidden {
    //display: none;
    transform: translate(0px, -200px);
  }
  &-row {
    @include flex-container(row, nowrap, center, space-between);
  }
  &-logo {
    @include flex(190px);
    height: 80px;

    text-decoration: none;
    &__stars {
      width: 46px;
      height: 10px;
      margin-bottom: -2px;

      background: url(../images/svg/stars.svg) no-repeat center;
      background-size: 100%;
    }
    &__name {
      width: 188px;
      height: 33px;
      margin-bottom: -6px;

      background: url(../images/svg/logo.svg) no-repeat center;
      background-size: 100%;
    }
    &__city {
      font-family: "Open Sans", "Arial", sans-serif;
      @include font(12px, 20px);
      letter-spacing: 0.3em;

      color: $gold;
    }
  }
  &__item {
    width: 894px;
    height: 76px;

    background: url(../images/temp/booking-dark.jpg) no-repeat top;
  }
}

@import "media";
