.restaurants-info {
  @media only screen and (max-width: 768px) {
    padding: 43px 0 60px;
    &-row {
      flex-wrap: wrap;
    }
    &-col {
      @include flex(100%);
      &:first-child {
        h2 {
          @include font(30px, 30px);
        }
        p {
          display: none;
        }
      }
      &:last-child {
        padding: 0;
        p.restaurants-info-note {
          display: block;
          max-width: 100%;
          margin-top: 21px;
          padding: 16px 0 16px 24px;
          border-left: 4px solid $gold;
        }
      }
    }
    &-title {
      @include font(30px, 30px);
    }
    &-note {
      display: none;
      &--mobile {
        display: block;
        max-width: 100%;
        margin-top: 21px;
        padding: 16px 0 16px 24px;
        border-left: 4px solid $gold;
      }
    }
  }
}
