.other-rooms {
  @include flex(100%);
  padding: 125px 0;

  background: $grey-lighter !important;
  &-row {
    @include flex-container(column);
  }
  &-list {
    @include flex-container();
    margin-top: 53px;
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &-outer {
      position: relative;

      margin-bottom: 19px;
    }
    &__item {
      position: relative;

      display: block;
      min-width: max-content;
      padding: 0 20px 19px;
      overflow: hidden;

      text-decoration: none;
      &--active {
        .other-rooms__title {
          color: $gold;
        }
      }
      &:hover {
        background: rgba(255, 255, 255, 0.05);
      }
      &-inner {
        position: relative;
        z-index: 5;

        display: block;
      }
      span.ripple {
        position: absolute;

        border-radius: 50%;

        background-color: #373739;

        transform: scale(0);
        animation: ripple 500ms ease-in-out;
      }
      @keyframes ripple {
        to {
          transform: scale(4);
          opacity: 0;
        }
      }
    }
  }
  &__title {
    color: #fff;
  }
  &__price {
    @include flex-container();

    color: $grey-lightest;
    &--star {
      &:after {
        content: "";
        display: block;
        width: 20px;
        height: 20px;
        margin-top: -2px;
        margin-left: 4px;

        background: url(../images/svg/star.svg) no-repeat, center;
      }
    }
  }
  &-preview {
    &-list {
      max-width: 1070px;
      &__item {
        display: none;
        &--active {
          display: block;
        }
        &-inner {
          @include flex-container($justify-content: space-between);
        }
      }
    }
    &-info {
      margin-top: 47px;
      @include flex(450px);
      &--mobile,
      &-detail {
        display: none;
      }
    }
    &__title {
      display: inline;

      @include font(40px, 40px);
      font-weight: 300;
      /*text-decoration: 1px #fff solid;
      text-decoration-line: underline;
      text-decoration-style: solid;
      text-decoration-color: #fff;
      text-decoration-thickness: 1px;*/
      text-decoration: none;

      color: #fff;

      background-image: linear-gradient(#fff, #fff);
      background-position: 0% 100%;
      background-repeat: no-repeat;
      background-size: 0% 1px;
      transition: background-size 0.3s;
      &:hover,
      &:focus {
        background-size: 100% 1px;
      }
    }
    &-features {
      @include flex-container(row, wrap, center, flex-start);
      align-items: center;
      margin: 30px 0 110px;
    }
    &-feature {
      @include flex(32%);
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      &__icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;

        background: url(../images/svg/area.svg) no-repeat center;
        &--bed {
          background-image: url(../images/svg/bed.svg);
        }
        &--bath {
          background-image: url(../images/svg/bath.svg);
        }
        &--table {
          background-image: url(../images/svg/table.svg);
        }
        &--drink {
          background-image: url(../images/svg/drink.svg);
        }
        &--armchair {
          background-image: url(../images/svg/armchair.svg);
        }
      }
      &__text {
        line-height: 15px;
      }
    }
    &__button {
      @include flex-container(row, nowrap, center);
    }
    &__price {
      @include font(14px, 15px);
      margin-left: 40px;
      &--big {
        @include font(25px, 40px);
      }
    }
    &-slider {
      @include flex(450px);
      height: 400px;
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
      &-image {
        max-width: 100%;
        max-height: 450px;
        overflow: hidden;
        margin: 0 auto;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin-top: 6px;
        @include flex-container(row, nowrap, normal, space-between);
        &-content {
          @include flex(150px);
          @include flex-container(row, nowrap, center, space-between);
        }
      }
      &-counter {
        margin-top: 13px;
      }
    }
  }
}

@import "media";
