.restaurants-info {
  @include flex(100%);
  padding: 120px 0;
  &-row {
    @include flex-container($justify-content: space-between);
  }
  &-col {
    @include flex(50%);
    &:first-child {
      h2 {
        margin-bottom: 19px;

        @include font(40px, 40px);
        font-weight: 300;
      }
      p {
        max-width: 450px;

        color: $grey-lightest;
      }
      p:not(.restaurants-info-note) {
        display: none;
      }
    }
    &:last-child {
      max-width: 420px;
      margin-top: 9px;
      h2 {
        display: none;
      }
      p.restaurants-info-note {
        display: none;
      }
    }
  }
  &-title {
    margin-bottom: 19px;

    @include font(40px, 40px);
    font-weight: 300;
    &--gold {
      color: $gold;
    }
  }
  &-note {
    max-width: 450px;

    color: $grey-lightest;
    &--mobile {
      display: none;
    }
  }
}

@import "media";
