.room {
  @media only screen and (max-width: 1280px) {
    &-info {
      &-titles {
        &-outer {
          margin-bottom: 37px;
          padding-bottom: 5px;
          overflow-x: scroll;
        }
      }
    }
  }
  @media only screen and (max-width: 1000px) {
    .section-title--room {
      @include font(46px, 54px);
    }
    &-col {
      @include flex(57%);
      &:first-child {
        @include flex(43%);
      }
    }
    &-feature {
      @include flex(50%);
    }
    &__button {
      flex-direction: column;
      align-items: normal;
      .btn {
        order: 1;
        max-width: 240px;
      }
    }
    &__price {
      margin-left: 0;
      margin-bottom: 20px;
    }
  }
  @media only screen and (max-width: 768px) {
    margin-bottom: 0;
    padding-top: 33px;
    .section-title--room {
      @include font(30px, 40px);
    }
    &-row {
      flex-wrap: wrap;
    }
    &-col,
    &-col:first-child {
      position: static;

      @include flex(100%);
    }
    &-features {
      display: none;
      &--mobile {
        display: flex;
        max-width: 100%;
        margin: 0;
        padding-bottom: 20px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        .room-feature {
          margin-bottom: 25px;
        }
      }
    }
    &__button,
    &__desc {
      display: none;
    }
    &-slider {
      &-container {
        position: relative;

        width: 100vw;
        max-height: 65vw;
        margin: 22px 0 19px -16px;
      }
      &-image {
        max-height: 65vw;
      }
      &-controls {
        position: absolute;
        top: 15px;
        right: 17px;

        margin: 0;

        background: rgba($color: $grey-lighter-2, $alpha: 0.9);
        &-content {
          display: none;
        }
      }
      &-counter {
        margin: 0;
        padding: 5px 10px;

        @include font(10px, 10px);
      }
    }
    &-info {
      &-titles {
        display: none;
      }
      &-preview {
        display: block;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);
        &-inner {
          display: none;
          &--shown {
            display: block;
          }
        }
        &__item {
          &:last-child {
            padding-bottom: 26px;
          }
        }
        &__title {
          position: relative;

          display: block;
          margin: 0;
          padding: 12px 0 17px;

          @include font(20px, 40px);
          font-weight: 300;
          &--shown {
            padding-bottom: 11px;
            .room-info-preview__icon {
              transform: rotate(180deg);
            }
          }
        }
        &__icon {
          position: absolute;
          top: 34.5px;
          right: 7px;

          display: inline-block;
          width: 10px;
          height: 5px;

          background: url(../images/svg/arrow-down-gold.svg) no-repeat center;
        }
      }
    }
    &-bottom {
      @media only screen and (max-width: 768px) {
        position: relative;
        z-index: 10;

        display: block;
        height: 70px;
        @include flex(100%);
        &-row {
          position: fixed;
          bottom: 0;

          width: 100vw;
          @include flex-container();
          height: 70px;
        }
        &__price {
          @include flex(50%);
          padding: 20px 16px;

          @include font(20px, 30px);
          font-weight: 300;

          background: $grey-lighter;
        }
        &__button {
          @include flex(50%);
          .btn {
            max-height: 70px;
            min-height: 70px;
            padding: 0 !important;

            line-height: 70px;
          }
        }
      }
    }
  }
  @media only screen and (max-width: 480px) {
    .room-feature {
      @include flex(50%);
    }
  }
}
