.header {
  @media only screen and (max-width: 1280px) {
    &-subnav-social {
      left: 2vw;
    }
  }
  @media only screen and (max-width: 1200px) {
    &-nav-list__item--lang {
      margin-left: 10px;
    }
    &-subnav {
      &-list {
        @include flex(270px);
      }
      &-social {
        left: 40px;
      }
    }
  }
  @media only screen and (max-width: 1000px) {
    border-bottom: 1px solid rgba(255, 255, 255, 0.1);
    &--small,
    &-form {
      display: none;
    }
    &--hidden {
      .header-info {
        transform: translate(0, 0) !important;

        transition: none;
      }
    }
    &-row {
      height: 70px;
      padding: 0;
      &--mobile {
        .header-nav-icon--close {
          background-image: url(../images/svg/close.svg);
        }
        .header-nav--mobile {
          .header-nav-list {
            @include flex-container(column, nowrap, normal, space-between);
          }
        }
        .header-contacts {
          &__button {
            display: none;
          }
          &-links--mobile {
            @include flex-container();
          }
        }
      }
    }
    &-logo {
      @include flex(150px);
      &__stars {
        margin-bottom: 2px;
      }
      &__name {
        width: 150px;
        height: 20px;
        margin-bottom: -2px;
      }
      &__city {
        font-size: 10px;
      }
    }
    &-info {
      @include flex(105px);
    }
    &-nav {
      order: -1;
      @include flex(0);
      &--mobile {
        display: block;
        .header-nav {
          &-icon {
            display: block;
          }
          &-list {
            position: absolute;
            left: 0;
            top: 71px;

            width: 100vw;
            height: calc(100vh - 70px);
            display: none;
            margin: 0;
            padding: 20px 16px 0;
            overflow-y: scroll;

            background: $grey-lighter;
          }
        }
      }
      &-list {
        display: none;
        &__item {
          padding: 5px 0;
          &--menu {
            display: none;
          }
        }
      }
      &-icon {
        display: none;
      }
      &__title {
        position: relative;

        display: block;
        margin: 0;
        padding: 12px 0 17px;
        border-bottom: 1px solid rgba(255, 255, 255, 0.1);

        @include font(20px, 20px);
        font-weight: 300;
        text-decoration: none;

        color: #fff;
      }
      &-more {
        position: absolute;
        top: 13px;
        right: 0;

        display: inline-block;
        width: 24px;
        height: 24px;
        background: url(../images/svg/arrow-more.svg) no-repeat center;
        &--shown {
          transform: rotate(180deg);
        }
      }
      &-sublist {
        display: none;
        margin-top: 15px;
        &--shown {
          display: block;
        }
        &__link {
          text-decoration: none;

          color: $grey;
        }
      }
    }
    &-contacts {
      @include flex(112px);
      &-links {
        display: none;
        &--mobile {
          display: none;
        }
      }
      &__button {
        display: block;
        .btn {
          line-height: 40px;
          height: 40px;
        }
      }
      &__value,
      &__link {
        display: none;
      }
      &__link--mobile {
        display: block;
        min-width: 40px;
        height: 40px;
        margin-right: 20px;
        border-radius: 50%;

        background: $gold url(../images/svg/call.svg) no-repeat center;
        &:first-child {
          background-image: url(../images/svg/login.svg);
        }
      }
    }
    &-form__item {
      height: 70px;
    }
    &-social {
      //position: absolute;
      //bottom: 0;

      padding: 25px 0 30px;
      &-links {
        @include flex-container();
        .header-social__link {
          display: block;
          width: 50px;
          height: 50px;
          margin-right: 35px;
          border-radius: 50%;

          background: $gold url(../images/svg/wa-white.svg) no-repeat center;
          &--facebook {
            background-image: url(../images/svg/facebook-white.svg);
          }
          &--inst {
            background-image: url(../images/svg/inst-white.svg);
          }
        }
      }
    }
  }
}
