.rooms-catalog {
  @include flex(100%);
  padding: 67px 0 119px;

  .container {
    position: relative;
  }
  &-items {
    margin-top: 53px;
  }
  &__item {
    position: relative;

    @include flex-container(row, nowrap, center, space-between);
    padding: 14px 0 20px 39px;
    border: 1px solid $grey;
    border-bottom: none;

    text-decoration: none;

    color: #fff;

    background: $grey-lighter-2 no-repeat center;
    background-size: cover;
    background-position-y: bottom;

    transition: background-position linear 10s;
    &:before {
      position: absolute;
      top: 0;
      left: 0;

      content: "";
      display: none;
      width: 100%;
      height: 100%;

      //background: rgba(0, 0, 0, 0.8);
      background: linear-gradient(
        0deg,
        rgba(30, 30, 30, 0.7),
        rgba(30, 30, 30, 0.7)
      );
    }
    &:after {
      position: absolute;
      top: 0;
      left: 0;

      content: "";
      display: none;
      width: 100%;
      height: 100%;

      background: $grey-lighter-2 no-repeat center;
      background-size: cover;
    }
    &--effects {
      background-position-y: top;
      &:before {
        display: block;
      }
    }
    &--border-top {
      border-top: none;
    }
    &:last-child {
      border-bottom: 1px solid $grey;
    }
    &:hover {
      border: none;
      .rooms-catalog {
        &-sum {
          margin-right: 33px;
        }
        &-more {
          display: block;
          margin-right: 26px;
        }
        &__desc {
          color: #fff;
        }
      }
    }
  }
  &-col {
    z-index: 5;
    &:last-child {
      @include flex-container(row, nowrap, center);
    }
  }
  &__name {
    margin-bottom: 4px;

    @include font(40px, 40px);
    font-weight: 300;

    color: #fff;
  }
  &__desc {
    color: $grey-lightest;
  }
  &-sum {
    margin-right: 60px;

    @include font(40px, 40px);
    font-weight: 300;

    color: #fff;
    &--mobile {
      display: none;
    }
  }
  &-more {
    display: none;
    min-width: 48px;
    min-height: 48px;
    border-radius: 50%;

    background: #fff url(../images/svg/arrow-gold.svg) no-repeat center;
  }
}

@import "media";
