.service {
  @include flex(100%);
  padding: 67px 0 120px;
  &-row {
    @include flex-container();
  }
  &-back {
    width: 100px;
    height: 60px;
    border: 1px solid $gold;
    margin-right: 200px;

    background: url(../images/svg/arrow-gold.svg) no-repeat center;

    transform: rotate(180deg);
  }
  &-text {
    margin: 17px 0 20px;
  }
  &-content {
    @include flex(550px);
  }
  &-gallery {
    &-row {
      @include flex-container(row, nowrap, normal, space-between);
      margin-top: 36px;
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
    }
    &-slider {
      &-row {
        margin: 0 -15px;
        @include flex-container(row, wrap, normal);
        max-width: 780px;
        transform: none !important;
        .service-gallery-slider-image {
          @include flex(156px);
          max-height: 79px;
          padding: 0 15px;
        }
        .service-gallery-slider__img {
          opacity: 0.8;
        }
        .service-gallery-slider-container {
          padding: 0 15px;
        }
      }
      &-container {
        @include flex(750px);
      }
      &-image {
        max-width: 750px;
        max-height: 405px;
        margin-top: 30px;
        overflow: hidden;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin: 6px 0 0;
        @include flex-container(row, nowrap, normal, space-between);
        &-col {
          @include flex-container(row, nowrap, center);
          &:first-child {
            @include flex(150px);
          }
          &:last-child {
            @include flex(150px);
          }
        }
      }
      &-counter {
        margin-right: 20px;
      }
    }
    &-info {
      @include flex(28.5%);
      padding-top: 52px;
      margin-right: 40px;
      &-text {
        margin-top: 22px;
      }
    }
  }
}
.service-price {
  @include flex(100%);
  padding: 127px 0 17px;
  &-list {
    @include flex-container(row, nowrap, center);
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    &-outer {
      position: relative;

      margin: 57px 0 48px;
    }
    &__item {
      min-width: max-content;
      padding: 0 40px 24px;

      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active,
      &:hover {
        //border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
  }
  &-preview {
    &-list {
      &__item {
        display: none;
        &--active {
          display: block;
        }
      }
    }
    &__title {
      @include font(30px, 40px);
      font-weight: 300;

      color: $gold;
    }
  }
  &-items {
    margin: 32px 0 44px;
  }
  &__item {
    @include flex-container(row, nowrap, center, space-between);
    padding: 14px 0 20px 39px;
    border: 1px solid $grey;
    &:last-child {
      border-top: none;
    }
    &:hover {
      border-color: $gold;

      background: $gold;
      .service-price {
        &-sum {
          margin-right: 33px;
        }
        &-plus {
          display: block;
          margin-right: 39px;
        }
        &__desc {
          color: #fff;
        }
      }
    }
  }
  &-col {
    &:last-child {
      @include flex-container(row, nowrap, center);
    }
  }
  &__name {
    margin-bottom: 4px;

    @include font(40px, 40px);
    font-weight: 300;
  }
  &__desc {
    color: $grey-lightest;
  }
  &-sum {
    margin-right: 60px;

    @include font(40px, 40px);
    font-weight: 300;
    &--mobile {
      display: none;
    }
  }
  &-plus {
    display: none;
    min-width: 48px;
    min-height: 48px;
    border-radius: 50%;

    background: #fff url(../images/svg/add-gold.svg) no-repeat center;
  }
  &-note {
    margin: 5px 0 41px;

    color: $grey-lightest;
  }
  &-button {
    @include flex-container($justify-content: center);
    margin-bottom: 108px;
  }
}

@import "media";
