.transfer-price {
  @include flex(100%);
  padding: 80px 0 115px;
  &__title {
    margin-bottom: 32px;

    @include font(30px, 40px);
    font-weight: 300;

    color: $gold;
  }
  &-items {
    margin-bottom: 23px;
  }
  &__item {
    @include flex-container(row, nowrap, normal, space-between);
    padding: 14px 0 20px 39px;
    border: 1px solid $grey;
    &:last-child {
      border-top: none;
    }
    &:hover {
      border-color: $gold;

      background: $gold;
      .transfer-price {
        &-sum {
          margin-right: 33px;
        }
        &-plus {
          display: block;
          margin-right: 39px;
        }
        &__desc {
          color: #fff;
        }
      }
    }
  }
  &-col {
    &:last-child {
      @include flex-container(row, nowrap, center);
    }
  }
  &__name {
    margin-bottom: 5px;

    @include font(40px, 40px);
    font-weight: 300;
  }
  &__desc {
    color: $grey-lightest;
  }
  &-sum {
    margin-right: 60px;

    @include font(40px, 40px);
    font-weight: 300;
    &--mobile {
      display: none;
    }
  }
  &-plus {
    display: none;
    width: 48px;
    height: 48px;
    border-radius: 50%;

    background: #fff url(../images/svg/add-gold.svg) no-repeat center;
  }
}

@import "media";
