.index-gallery {
  @include flex(100%);
  margin-bottom: 120px;
  .tns {
    &-nav + button,
    &-controls {
      display: none;
    }
    &-nav {
      & > button {
        display: none;
      }
    }
  }
  &-row {
    @include flex-container(row, nowrap, normal, space-between);
    margin-top: 45px;
  }
  &-slider {
    &-row {
      margin: 0 -15px;
      @include flex-container(row, wrap, normal);
      max-width: 780px;
      transform: none !important;
      .index-gallery-slider-image {
        @include flex(156px);
        max-height: 79px;
        padding: 0 15px;
      }
      .index-gallery-slider__img {
        opacity: 0.8;
      }
      .index-gallery-slider-container {
        padding: 0 15px;
      }
    }
    &-container {
      @include flex(62.5%);
    }
    &-image {
      max-width: 750px;
      height: 405px;
      margin-top: 30px;
      overflow: hidden;
    }
    &__img {
      @include img();
    }
    &-controls {
      margin: 6px 0 0;
      @include flex-container(row, nowrap, normal, space-between);
      &-col {
        @include flex-container(row, nowrap, center);
        &:first-child {
          @include flex(150px);
        }
        &:last-child {
          @include flex(150px);
        }
      }
    }
    &-counter {
      margin-right: 20px;
    }
  }
  &-info {
    @include flex(28.5%);
    padding-top: 52px;
    margin-right: 40px;
    &-text {
      margin-top: 22px;
    }
  }
}

@import "media";
