.service {
  @media only screen and (max-width: 1100px) {
    &-row {
      justify-content: space-between;
    }
    &-back {
      margin: 0;
    }
    &-content {
      @include flex(75%);
    }
    &-gallery-slider-container {
      @include flex(100%);
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 33px 0 60px;
    &-row {
      flex-wrap: wrap;
      .slider__icon {
        display: none;
      }
    }
    &-back {
      display: none;
    }
    &-content {
      @include flex(100%);
    }
    &-text {
      margin: 11px 0 36px;
    }
    &-gallery {
      &-row {
        margin-top: 0;
        margin-left: -16px;
        width: 100vw;
      }
      &-slider {
        &-container {
          position: relative;

          @include flex(100vw);
          max-height: 65vw;
        }
        &-image {
          max-height: 65vw;
        }
        &-controls {
          position: absolute;
          top: 15px;
          right: 17px;

          margin: 0;

          background: rgba($color: $grey-lighter-2, $alpha: 0.9);
          &-col {
            &:first-child {
              @include flex(auto);
            }
            &:last-child {
              display: none;
            }
          }
        }
        &-image {
          max-width: 100%;
          height: auto;
          margin-top: 0;
        }
      }
    }
    &-slider-counter {
      margin: 0;
      padding: 5px 10px;

      @include font(10px, 10px);
    }
  }
}

.service-price {
  @media only screen and (max-width: 1100px) {
    &-col {
      @include flex(50%);
      &:last-child {
        justify-content: flex-end;
      }
    }
    &-sum {
      margin-right: 40px;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 43px 0 46px;
    &-list {
      overflow-x: scroll;
      &-outer {
        margin: 26px 0 29px;
      }
      &__item {
        min-width: max-content;
        padding: 0 20px 24px;
      }
    }
    &-items {
      margin-bottom: 14px;
    }
    &__item {
      padding: 0;
      &:hover {
        background: transparent;
        .service-price {
          &-sum {
            margin-right: 0;
          }
          &-plus {
            display: block;
            margin-right: auto;
          }
        }
      }
    }
    &-col {
      @include flex(20%);
      min-height: 67px;
      @include flex-container(column, nowrap, normal, center);
      padding: 7px 0 12px;
      &:first-child {
        @include flex(80%);
        padding: 7px 0 12px 19px;
        border-right: 1px solid $grey;
      }
    }
    &__name {
      font-size: 20px;
      margin-bottom: -8px;
    }
    &-sum {
      display: none;
      &--mobile {
        display: block;
        margin: 0;

        @include font(20px, 40px);
      }
    }
    &-plus {
      display: block;
      margin: 0 auto;

      background: $gold url(../images/svg/add.svg) no-repeat center;
    }
    &-note {
      margin-bottom: 0;
    }
    &-button {
      display: none;
    }
  }
}
