.rooms-catalog {
  @media only screen and (max-width: 1100px) {
    &-col {
      @include flex(50%);
      &:last-child {
        justify-content: flex-end;
      }
    }
    &-sum {
      margin-right: 40px;
    }
  }
  @media only screen and (max-width: 768px) {
    padding: 43px 0 46px;
    &-items {
      margin-top: 30px;
    }
    &__item {
      padding: 0;
      &:hover {
        background: transparent;
        .rooms-catalog {
          &-sum {
            margin-right: 0;
          }
          &-more {
            display: block;
            margin-right: auto;
          }
        }
      }
    }
    &-col {
      @include flex(20%);
      min-height: 67px;
      @include flex-container(column, nowrap, normal, center);
      padding: 7px 0 12px;
      &:first-child {
        @include flex(80%);
        padding: 7px 0 12px 19px;
        border-right: 1px solid $grey;
      }
    }
    &__name {
      font-size: 20px;
      margin-bottom: -8px;
    }
    &-sum {
      display: none;
      &--mobile {
        display: block;
        margin: 0;

        @include font(20px, 40px);
      }
    }
    &-more {
      display: block;
      margin: 0 auto;

      background: $gold url(../images/svg/arrow.svg) no-repeat center;
    }
  }
}
