.rooms {
  @include flex(100%);
  padding: 125px 0 115px;

  background: $grey-darker;
  .section-title--rooms {
    display: none;
  }
  &-row {
    @include flex-container(row, nowrap, normal, space-between);
  }
  &-list {
    position: relative;

    @include flex-container(column);
    flex-basis: 210px;
    border-left: 1px solid rgba(255, 255, 255, 0.1);
    &-outer {
      position: relative;

      margin-top: 35px;
    }
    &__item {
      position: relative;

      display: block;
      padding: 10px 5px 10px 28px;
      margin-bottom: 10px;
      overflow: hidden;

      text-decoration: none;
      &--active {
        .rooms__title {
          color: $gold;
        }
      }
      &:hover {
        background: rgba(255, 255, 255, 0.05);
      }
      &-inner {
        position: relative;
        z-index: 5;

        display: block;
      }
      span.ripple {
        position: absolute;

        border-radius: 50%;

        background-color: #373739;

        transform: scale(0);
        animation: ripple 500ms ease-in-out;
      }
      @keyframes ripple {
        to {
          transform: scale(4);
          opacity: 0;
        }
      }
    }
  }
  .underline {
    position: absolute;
    top: 0;

    width: 2px;
    height: 100px;
  }
  &__title {
    color: #fff;
  }
  &__price {
    color: $grey-lightest;
  }
  &-preview {
    &-list {
      @include flex(77%);
      &__item {
        display: none;
        &--active {
          display: block;
        }
        &-inner {
          @include flex-container();
          justify-content: flex-end;
        }
      }
    }
    &-info {
      @include flex(33%);
      margin-top: 15px;
      margin-right: 17px;
      &--mobile {
        display: none;
      }
      &-detail {
        display: none;
      }
    }
    &__price {
      @include font(14px, 15px);
      margin-bottom: 10px;
      &--big {
        @include font(25px, 40px);
      }
    }
    &__title {
      @include font(40px, 40px);
      font-weight: 300;
      //text-decoration: 1px #fff solid;
      //text-decoration-line: underline;
      //text-decoration-style: solid;
      //text-decoration-color: #fff;
      //text-decoration-thickness: 1px;

      color: #fff;
      text-decoration: none;

      background-image: linear-gradient(#fff, #fff);
      background-position: 0% 100%;
      background-repeat: no-repeat;
      background-size: 0% 1px;
      transition: background-size 0.3s;
      &:hover,
      &:focus {
        background-size: 100% 1px;
      }
    }
    &-features {
      @include flex-container(row, wrap, center, flex-start);
      align-items: center;
      margin: 38px 0 105px;
    }
    &-feature {
      @include flex(50%);
      display: flex;
      align-items: center;
      margin-bottom: 20px;
      &__icon {
        display: inline-block;
        width: 20px;
        height: 20px;
        margin-right: 10px;

        background: url(../images/svg/area.svg) no-repeat center;
        &--bed {
          background-image: url(../images/svg/bed.svg);
        }
        &--bath {
          background-image: url(../images/svg/bath.svg);
        }
        &--table {
          background-image: url(../images/svg/table.svg);
        }
        &--drink {
          background-image: url(../images/svg/drink.svg);
        }
        &--armchair {
          background-image: url(../images/svg/armchair.svg);
        }
      }
      &__text {
        line-height: 15px;
      }
    }
    &-slider {
      width: 600px;
      height: 500px;
      .tns {
        &-nav + button,
        &-controls {
          display: none;
        }
        &-nav {
          & > button {
            display: none;
          }
        }
      }
      &-image {
        max-width: 100%;
        max-height: 450px;
        overflow: hidden;
        margin: 0 auto;
      }
      &__img {
        @include img();
      }
      &-controls {
        margin-top: 6px;
        @include flex-container(row, nowrap, normal, space-between);
        &-content {
          @include flex(150px);
          @include flex-container(row, nowrap, center, space-between);
        }
      }
    }
  }
  &-slider {
    &-counter {
      margin-top: 13px;
    }
  }
}

@import "media";
