.room {
  @include flex(100%);
  margin-bottom: 112px;
  padding-top: 67px;
  &-row {
    position: relative;

    @include flex-container($justify-content: flex-end);
  }
  &-col {
    @include flex(50%);
    &-sticky {
      position: sticky;
      top: 150px;
    }
  }
  &-features {
    @include flex-container(row, wrap, center, space-between);
    margin: 45px 0 40px;
    max-width: 450px;
    &--mobile {
      display: none;
    }
  }
  &-feature {
    @include flex(33%);
    display: flex;
    margin-bottom: 30px;
    &__icon {
      display: inline-block;
      width: 20px;
      height: 20px;
      margin-right: 10px;

      background: url(../images/svg/area.svg) no-repeat center;
      &--bed {
        background-image: url(../images/svg/bed.svg);
      }
      &--bath {
        background-image: url(../images/svg/bath.svg);
      }
      &--table {
        background-image: url(../images/svg/table.svg);
      }
      &--drink {
        background-image: url(../images/svg/drink.svg);
      }
      &--armchair {
        background-image: url(../images/svg/armchair.svg);
      }
    }
  }
  &__button {
    @include flex-container(row, nowrap, center);
  }
  &__price {
    margin-left: 40px;

    @include font(30px, 40px);
  }
  &__desc {
    margin: 22px 0 45px;
    max-width: 450px;
  }
  .tns {
    &-nav + button,
    &-controls {
      display: none;
    }
    &-nav {
      & > button {
        display: none;
      }
    }
  }
  &-slider {
    &-container {
      width: 100%;
      margin-bottom: 115px;
    }
    &-image {
      max-width: 100%;
      max-height: 450px;
      overflow: hidden;
      margin: 0 auto;
    }
    &__img {
      @include img();
    }
    &-controls {
      margin-top: 10px;
      @include flex-container(row, nowrap, normal, space-between);
      &-content {
        @include flex(130px);
        @include flex-container(row, nowrap, center, space-between);
        margin-top: 15px;
      }
    }
  }
  &-info {
    &-titles {
      @include flex-container();
      border-bottom: 1px solid rgba(255, 255, 255, 0.2);
      &-outer {
        position: relative;

        margin-bottom: 42px;
      }
    }
    &__title {
      padding: 0 60px 22px;
      min-width: max-content;
      @include font(15px, 30px);
      text-decoration: none;

      color: #fff;
      &--active {
        //border-bottom: 2px solid $gold;

        color: $gold;
      }
    }
    &-preview {
      display: none;
      &--active {
        display: block;
      }
      &__title {
        display: none;
      }
      &__item {
        @include flex-container();
        &:before {
          content: "";
          display: inline-block;
          min-width: 24px;
          height: 30px;
          margin-right: 10px;

          background: url(../images/svg/check.svg) no-repeat center;
        }
      }
      &__name {
        @include font(15px, 35px);
      }
    }
  }
  &-bottom {
    display: none;
  }
}

@import "media";
